<template>
  <!-- 新闻 -->
  <div class="news">
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <span class="card-title">News</span>
      </div>
      <el-row style="text-align:center">
        <div v-for="(item,index) in list" :key="index" class="text item">
            <p to="/news/newsDetail" @click="target(index)">{{item.title}}</p>
        </div>
      </el-row>
    </el-card>
  </div>
</template>

<script>
import list from '../static/news'
export default {
  name: 'News',
  data () {
    return {
      list: list
    }
  },
  methods: {
    target (index) {
      this.$store.commit('news/selectIndex', index)
      this.$router.push('/news/newsDetail')
    }
  }
}
</script>

<style lang="scss" scoped>
@import "../assets/variables.scss";
.news {
  .text {
    font-size: 14px;
  }
  .item {
    margin-bottom: 18px;
  }
  .clearfix:before,
  .clearfix:after {
    display: table;
    content: "";
  }
  .clearfix:after {
    clear: both;
  }

  .box-card {
    a {
      border-radius: 4px;
      color: #ddc597;
      font-weight: 700;
      word-break: break-all;
    }
  }
}
</style>



